/**
 *   InputWrap (label)
 *     Icon (svg)
 *     InputEle (input)
 *     Icon (svg)
 *     InputError (span)
 */

import styled, { css } from 'styled-components';
import { COLORS } from '../../common/consts/colors';
import { TEXT_CSS } from '../../basic/text';
import { IInputProps } from './Input';
import { CSS_BORDER_BOX, CSS_CURSOR_DISABLE } from '../../common';

const CSS_BORDERED_ENABLED = css`
  &:hover {
    border-color: ${COLORS.COMMON.BLUE1_COLOR};
  }
  &:active {
    border-color: ${COLORS.COMMON.BLUE1_COLOR};
  }
  &:focus-within {
    border-color: ${COLORS.COMMON.BLUE1_COLOR};
  }
`;

const CSS_BORDERED_DANGER = css`
  &:hover {
    border-color: ${COLORS.COMMON.DANGER1_COLOR};
  }
  &:active {
    border-color: ${COLORS.COMMON.DANGER1_COLOR};
  }
  &:focus-within {
    border-color: ${COLORS.COMMON.DANGER1_COLOR};
  }
`;

type IInputWrapProps = Required<Pick<IInputProps, 'bordered' | 'size' | 'disabled'>> & { error: boolean };

export const InputWrap = styled.label<IInputWrapProps>`
  display: flex;
  flex-wrap: keep-all;
  align-items: center;

  width: 100%;
  height: ${({ size }) => (size === 'normal' ? '32px' : '28px')};

  padding: 0 4px;
  ${({ error }) => error ? 'padding-top: 2px;' : null}  /* errorMessage 布局问题，改善 DOM 结构后移除之 */

  color: ${({disabled})=> disabled ? COLORS.LIGHT.LEVEL4_BASE_COLOR : COLORS.LIGHT.LEVEL1_BASE_COLOR};
  background-color: ${({disabled, bordered}) => bordered ? ( disabled ? '#f6f6f6' : COLORS.WHITE ) : COLORS.TRANSPARENT };

  border-radius: 4px;
  border: 1px solid ${COLORS.COMMON.OVERLAY_COLOR};
  ${({ disabled, error }) => !disabled ? !error ? CSS_BORDERED_ENABLED : CSS_BORDERED_DANGER : null}
  ${({ bordered }) => !bordered ? 'border: none;' : null}

  &:focus-within {
    ${({ bordered, disabled, error }) =>
    (bordered && !disabled && !error) ?
      'box-shadow: 0px 0px 0px 2px rgba(0, 106, 254, 0.12)' :
      (bordered && !disabled && error) ?
        'box-shadow: 0px 0px 0px 2px rgba(255, 82, 25, 0.12)' :
        null
    }
  }

  &:hover {
    .icon-close {
      display: block;
    }
  }
  
  ${TEXT_CSS.TEXT}
  ${CSS_BORDER_BOX}
  ${CSS_CURSOR_DISABLE}

  .icon-close {
    display: none;
    color: rgba(23, 26, 29, 0.4);
  
    &:hover {
      color: rgba(23, 26, 29, 1);
    }
  }
`;


export const InputEle = styled.input`
  color: ${COLORS.LIGHT.LEVEL1_BASE_COLOR};
  width: 0;
  flex: 1 auto;
  margin: 0;
  padding: 0 4px;
  outline: none;
  border: 0;
  background-color: ${COLORS.TRANSPARENT};
  &::placeholder {
    color: ${COLORS.LIGHT.LEVEL4_BASE_COLOR};
  }
  &:disabled {
    cursor: not-allowed;
    color: ${COLORS.LIGHT.LEVEL4_BASE_COLOR};
  }

  &::selection {
    background:#0089ff;
    color: ${COLORS.WHITE};
  }
`;

export const InputErrorMessage = styled.div`
  width: 100%;
  
  margin-top: 4px;

  font-size: 12px;
  line-height: 18px;

  overflow: hidden;		
  text-overflow: ellipsis;		
  white-space: nowrap;
  
  color: ${COLORS.COMMON.DANGER1_COLOR};
`;
